<template>
  <div class="projects">
    <div class="container">
      <transition name="header-project" appear>
        <h1 class="header-container">My projects</h1>
      </transition>
      <transition name="feature" appear>
        <div class="feature-card">
          <h1>Featured Project</h1>
          <FeaturedCard class="card-featured" :project="featuredProject" />
        </div>
      </transition>
      <div class="projects-card">
        <h1>My projects</h1>
        <ProjectCard :projects="projects" v-if="!isTouchScreen" />
        <TouchScreenCard :projects="projects" v-if="isTouchScreen" />
      </div>
    </div>
  </div>
</template>

<script>
import FeaturedCard from '@/components/FeaturedCard';
import ProjectCard from '@/components/ProjectCard';
import TouchScreenCard from '@/components/TouchScreenCard';

export default {
  components: {
    FeaturedCard,
    ProjectCard,
    TouchScreenCard,
  },
  data() {
    return {
      featuredProject: {
        name: 'Vuetter',
        description:
          'Twitter like web page',
        image: 'vuetter1.jpg',
        url: 'vuetter',
      },
      projects: [
        {
          name: 'Vuetter',
          description: 'Twitter like web page',
          image: 'vuetter1.jpg',
          url: 'vuetter'
        },
        {
          name: 'React chat',
          description: 'A chat app made with react',
          image: 'react-chat1.jpg',
          url: 'react-chat'
        },
        {
          name: 'Recipes App',
          description: 'Recipes app built with Vue',
          image: 'vue-recipes1.png',
          url: 'recipes-vue',
        },
        {
          name: 'Vue blog',
          description: 'A blog made with Vue',
          image: 'vue-blog1.png',
          url: 'vue-blog',
        },
        {
          name: 'PHP Library app',
          description: 'A library website made with PHP',
          image: 'php-books1.jpg',
          url: 'php-library'
        },
        {
          name: 'React inventory app',
          description: 'Inventory app using React',
          image: 'react-inventory1.png',
          url: 'react-inventory'
        },
        {
          name: 'Node Blog',
          description: 'A blog application built with Node',
          image: 'node-blog1.png',
          url: 'node-blog',
        },
        {
          name: 'Anime search app',
          description: 'An anime search app made with Vue',
          image: 'vue-anime1.png',
          url: 'vue-anime-search',
        },
        {
          name: 'Vue landing page',
          description: 'A landing page built with Vue',
          image: 'vue-landing1.png',
          url: 'vue-landing-page',
        }
      ],
    };
  },
  computed: {
    isTouchScreen() {
      if (
        'ontouchstart' in window ||
        navigator.maxTouchPoints > 0 ||
        navigator.msMaxTouchPoints > 0
      ) {
        return true;
      }
      return false;
    },
  },
};
</script>

<style lang="scss" scoped>
.projects {
  min-height: calc(100vh - 80px);
  display: grid;
  grid-template-columns: 10% 1fr 10%;

  .container {
    display: grid;
    grid-template-rows: auto minmax(50vh, auto) minmax(50vh, auto);
    grid-column: 2 / 3;

    .header-container {
      font-size: 4rem;
      text-align: center;
    }

    .feature-card {
      grid-row: 2 / 3;
     
      .card-featured {
        height: 80%;
      }
    }

    .projects-card {
      margin-bottom: 10px;
    }
  }
}

.header-project-enter-active {
  animation: dropDown 1s;
}

.feature-enter-active {
  opacity: 0;
  animation: toRight 1s;
  animation-delay: 0.5s;
}

.project-card-enter-active {
  opacity: 0;
  animation: toLeft 1s;
  animation-delay: 1s;
}

@keyframes dropDown {
  0% {
    transform: translateY(-50%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes toRight {
  0% {
    transform: translateX(-50%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes toLeft {
  0% {
    transform: translateX(50%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
</style>
