<template>
  <transition-group
    name="card-transition"
    appear
    tag="div"
    class="template-card"
  >
    <div class="card" v-for="(project, index) in projects" :key="index" :style="`--i:${index}`">
      <div class="card-img">
        <img :src="require('@/assets/img/' + project.image + '')" alt="" />
      </div>
      <div class="card-text">
        <h2>{{ project.name }}</h2>
        <p>
          {{ project.description }}
        </p>
        <router-link :to="`/project/${project.url}`" class="project-link"
          >See project</router-link
        >
      </div>
      <div class="overlay"></div>
    </div>
  </transition-group>
</template>

<script>
export default {
  props: ['projects'],
};
</script>

<style lang="scss" scoped>
.template-card {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;

  .card {
    position: relative;
    box-shadow: 2px 4px 8px 2px rgba($color: #000000, $alpha: 0.5);

    .card-img {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .card-text {
      position: absolute;
      left: 0;
      bottom: 0;
      top: 0;
      right: 0;
      margin: 0;
      opacity: 0;
      transition: 0.5s ease-in all;
      z-index: 1;
      text-align: center;

      h2 {
        margin-bottom: 0;
      }

      p {
        font-size: 1.5rem;
        margin: 5px 0;
      }

      .project-link {
        background-color: $primary-color;
        padding: 10px 15px;
        font-size: 1.2rem;
        border: none;
        border-radius: 12px;
        color: $bg-color;
        text-decoration: none;
        margin-bottom: 5px;
        cursor: pointer;
        transition: 0.5s ease-in all;

        &:hover {
          background-color: $third-color;
        }
      }
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(255, 255, 255, 0.7);
      opacity: 0;
      transition: 0.5s ease-in all;
    }

    &:hover {
      .card-text {
        opacity: 1;
      }

      .overlay {
        opacity: 1;
      }
    }
  }

  .card-transition-enter-active {
    transition: all calc(var(--i) * 0.5s);
  }

  .card-transition-enter {
    opacity: 0;
    transform: translateX(50%);
  }
}

@media (max-width: 992px) {
  .template-card {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 600px) {
  .template-card {
    grid-template-columns: 1fr;
  }
}
</style>
