<template>
  <transition-group name="touch-transition" appear tag="div" class="template-touch-card">
    <div class="touch-card" v-for="(project, index) in projects" :key="index" :style="`--i:${index}`">
      <div class="touch-card-img">
        <img :src="require('@/assets/img/'+project.image+'')" alt="" />
      </div>
      <div class="touch-card-text">
        <h1>{{project.name}}</h1>
        <p>
          {{project.description}}
        </p>
        <router-link :to="`/project/${project.url}`" class="touch-project-link">See project</router-link>
      </div>
    </div>
  </transition-group>
</template>

<script>
export default {
  name: 'TouchScreenCard',
  props: ['projects']
};
</script>

<style lang="scss" scoped>
.template-touch-card {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;

  .touch-card {
    box-shadow: 2px 4px 8px 2px rgba($color: #000000, $alpha: 0.5);
    padding: 10px;

    .touch-card-img {
      img {
        width: 100%;
        object-fit: cover;
      }
    }

    .touch-card-text {
      margin-bottom: 10px;

      h1 {
        font-size: 1.5rem;
      }

      .touch-project-link {
        background-color: $primary-color;
        padding: 10px 15px;
        font-size: 1.2rem;
        border: none;
        border-radius: 12px;
        color: $bg-color;
        text-decoration: none;
        cursor: pointer;
      }
    }
  }

  .touch-transition-enter-active {
    transition: all calc(var(--i) * 0.5s);
  }

  .touch-transition-enter {
    opacity: 0;
    transform: translateX(50%);
  }
}

@media (max-width: 992px) {
  .template-touch-card {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 600px) {
  .template-touch-card {
    grid-template-columns: 1fr;
  }
}
</style>
